import request from '@/request/http';

// 购物车
export function queryShopCart(query) {
    return request({
        url: '/ssm/weChat/queryShopCart',
        method: 'get',
        params: query
    })
}

// 加入购物车
export function addShopCart(query) {
    return request({
        url: '/ssm/weChat/addShopCart',
        method: 'get',
        params: query
    })
}

// 购物车数量和总价
export function queryPrice(query) {
    return request({
        url: '/ssm/weChat/queryPrice',
        method: 'get',
        params: query
    })
}

// 删除购物车
export function deleteShopCart(query) {
    return request({
        url: '/ssm/weChat/deleteShopCart',
        method: 'get',
        params: query
    })
}



