<template>
  <div class="item">
    <div class="img">
      <img :src="`/picture/${item.itemImg}`" alt="" />
    </div>
    <div class="info">
      <div class="top">
        <p class="name">{{ item.itemName }}</p>
        <p class="tip">{{ item.goodsTexture }}</p>
      </div>
      <div class="price">
        ￥{{ item.itemPrice }} <span class="cm">/件</span>
      </div>
    </div>
    <div class="buy-num">
      <i class="add" @click="_buy(item, 'add')"></i>
      <span class="num">{{ item.num }}</span>
      <i class="less" @click="_buy(item, 'less')"></i>
    </div>
  </div>
</template>
<script>
import { addShopCart, deleteShopCart } from "@/api/cart.js";
import _ from 'lodash';
export default {
  data() {
    return {
        isSubmit: true,
    };
  },
  props: {
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  created() {
  },
  methods: {
	_buy: _.debounce(function(item, type) {
		if (type === 'add') {
			this._add(item);
		}
		if (type === 'less') {
			this._less(item);
		}
	}, 500),
    _add(item) {
        if (this.item.num < 100) {
            if (!this.isSubmit) {
                return;
            }
            this.isSubmit = false;
            let params = {
                itemId: item.itemId,
                userId: 1,
                id: this.item.num ? item.id : null,
                flag: 1
            };
            const toast = this.$createToast({
                time: 5000
            });
            toast.show();
            addShopCart(params).then(res => {
                this.item.num += 1;
                toast.hide();
				this.isSubmit = true;
				item.id = res.id;
            });
        }
    },
    _less(item) {
        if (this.item.num > 0) {
            if (!this.isSubmit) {
                return;
            }
            this.isSubmit = false;
            let params = {
                itemId: item.itemId,
                userId: 1,
                id: item.id,
                flag: 2
            };
            const toast = this.$createToast({
                time: 5000
            });
            toast.show();
            addShopCart(params).then(res => {
                if (this.item.num === 1) {
                    this._deleteShopCart(item.id);
                }
                this.item.num -= 1;
                toast.hide();
                this.isSubmit = true;
            });
        }
    },
    // 删除商品
    _deleteShopCart(shopId) {
        let params = {
            shopId
        };
        deleteShopCart(params).then(res => {});
    }
  }
};
</script>
<style lang="stylus" scoped>
.item {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 20px 0;
  border-bottom: 0.5px solid #F2F2F2;
  position: relative;

  .img {
    width: 180px;
    height: 120px;
    border-radius: 8px;

    img {
      width: 180px;
      height: 120px;
      border-radius: 8px;
    }
  }

  .info {
    height: 120px;
    padding-left: 21px;

    .top {
      margin-top: 10px;
      position: relative;

      .name {
        vertical-align: top;
        display: inline-block;
        font-size: 28px;
        font-family: HiraginoSansGB-W3;
        color: #333333;
      }

      .tip {
        vertical-align: top;
        display: inline-block;
        font-size: 18px;
        line-height: 24px;
        font-family: HiraginoSansGB-W3;
        color: #ffffff;
        padding: 0 6px;
        background: #F89771;
        border-radius: 8px;
      }
    }

    .price {
      margin-top: 45px;
      font-size: 24px;
      font-family: HiraginoSansGB-W3;
      font-weight: bold;
      color: #F43030;

      .cm {
        font-size: 18px;
        color: #666666;
      }
    }
  }

  .buy-num {
    position: absolute;
    right: 0px;

    .add {
      display: inline-block;
      width: 40px;
      height: 40px;
      background-image: url('../../../static/image/product/add@2x.png');
      background-size: 100% 100%;
      background-repeat: no-repeat;
    }

    .less {
      display: inline-block;
      width: 40px;
      height: 40px;
      background-image: url('../../../static/image/product/js@2x.png');
      background-size: 100% 100%;
      background-repeat: no-repeat;
    }

    .num {
      vertical-align: top;
      display: inline-block;
      font-size: 24px;
      font-family: HiraginoSansGB-W3;
      font-weight: bold;
      color: #333333;
      height: 40px;
      line-height: 40px;
      padding: 0 14px;
    }
  }
}
</style>


