<template>
  <div id="app">
    <!-- <transition> -->
      <!-- <keep-alive> -->
        <router-view></router-view>
      <!-- </keep-alive> -->
    <!-- </transition> -->
  </div>
</template>

<script>

export default {
  name: 'App',
};
</script>

<style lang="stylus">
@import 'nprogress/nprogress.css'

html {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
}
body, #app {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  background-color: #F2F2F2;
  font-size: 32px;
}
* {
  box-sizing: border-box;

  &:before,
  &:after {
    box-sizing: border-box;
  }
}
img {
  width: 100%;
  height: auto;
  vertical-align: top;
  content: normal !important;
}
  [aspectratio] {
	  position: relative;
  }
  [aspectratio]::before {
	  content: '';
	  display: block;
	  width: 1px;
	  margin-left: -1px;
	  height: 0;
  }

  [aspectratio-content] {
	  position: absolute;
	  top: 0;
	  left: 0;
	  right: 0;
	  bottom: 0;
	  width: 100%;
	  height: 100%;
  }
  [flexContainer] {
    display: flex;
    width: 750px;
  }
  ul,li {
    list-style: none outside none;
    margin: 0;
    padding: 0;
  }
  figure {
    display: block;
    margin: 0;
  }
  
@svg 1px-border {
	  height: 2px;
	  @rect {
		  fill: var(--color, black);
		  width: 100%;
		  height: 50%;
	  }
	}
</style>



