import Vue from 'vue'
import Router from 'vue-router'

const login = resolve => require(['@/views/login'], resolve);

const home = resolve => require(['@/views/home/index'], resolve);
const search = resolve => require(['@/views/search/index'], resolve);
const productSelect = resolve => require(['@/views/product-select/index'], resolve);
const shoppingCart = resolve => require(['@/views/shopping-cart'], resolve);
const center = resolve => require(['@/views/center'], resolve);
const recharge = resolve => require(['@/views/recharge'], resolve);
const pay = resolve => require(['@/views/pay'], resolve);
const order = resolve => require(['@/views/order'], resolve);
const myAddress = resolve => require(['@/views/my-address'], resolve);
const addressEdit = resolve => require(['@/views/address-edit'], resolve);
const myOrder = resolve => require(['@/views/my-order'], resolve);
const orderDetail = resolve => require(['@/views/order-detail'], resolve);
const categorySelect = resolve => require(['@/views/category-select'], resolve);

const demo1 = resolve => require(['@/views/demo/demo1'], resolve);
const demo2 = resolve => require(['@/views/demo/demo2'], resolve);

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      title: '',
      component: home,
      meta: {
        title: ''
      },
      // redirect: '/demo1',
      // children: [
      //   {
      //     path: 'demo1',
      //     name: 'demo1',
      //     component: demo1,
      //   },
      //   {
      //     path: 'demo2',
      //     name: 'demo2',
      //     component: demo2,
      //   }
      // ]
    },
    {
      path: '/search',
      name: 'search',
      meta: {
        title: ''
      },
      component: search,
    },
    {
      path: '/product-select',
      name: 'productSelect',
      meta: {
        title: ''
      },
      component: productSelect,
    },
    {
      path: '/login',
      name: 'login',
      meta: {
        title: '登录',
      },
      component: login
    },
    {
      path: '/shopping-cart',
      name: 'shopping-cart',
      meta: {
        title: '购物车'
      },
      component: shoppingCart,
    },
    {
      path: '/center',
      name: 'center',
      meta: {
        title: '个人中心'
      },
      component: center,
    },
    {
      path: '/recharge',
      name: 'recharge',
      meta: {
        title: '在线充值'
      },
      component: recharge,
    },
    {
      path: '/pay',
      name: 'pay',
      meta: {
        title: '支付'
      },
      component: pay,
    },
    {
      path: '/order',
      name: 'order',
      meta: {
        title: '订单'
      },
      component: order,
    },
    {
      path: '/my-address',
      name: 'my-address',
      meta: {
        title: '我的地址'
      },
      component: myAddress,
    },
    {
      path: '/address-edit',
      name: 'address-edit',
      meta: {
        title: '编辑地址'
      },
      component: addressEdit,
    },
    {
      path: '/my-order',
      name: 'my-order',
      meta: {
        title: '我的订单'
      },
      component: myOrder,
    },
    {
      path: '/order-detail',
      name: 'order-detail',
      meta: {
        title: '订单详情'
      },
      component: orderDetail,
    },
    {
      path: '/category-select',
      name: 'category-select',
      meta: {
        title: ''
      },
      component: categorySelect,
    },
  ]
})



