<style lang="stylus" scoped>

@svg pxborder {
	height: 2px;
	@rect {
		fill: var(--color, black);
	    width: 100%;
		height: 50%;
	}
}

.layout{
    width: 100%;
    height: 100vh;
    background-color: #F2F2F2;
    .nav {
        position: fixed;
        z-index: 99;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 98px;
        display: flex;
        justify-content: center;
        align-items: center
        background: rgba(255,255,255,1);
        border-top: 0.5px solid #F2F2F2;
        .nav-link {
