                .home {
                    background-image: url(../../../static/image/common/icon_index@2x.png);
                }
                .order {
                    background-image: url(../../../static/image/common/icon_dd_sel@2x.png);
                }
                .car {
                    background-image: url(../../../static/image/common/icon_gwc_sel@2x.png);    
                }
                .my {
                    background-image: url(../../../static/image/common/icon_mine_sel@2x.png);
                }
            }
        }
    }
}
</style>




